import { React, Fragment, useState, useLayoutEffect } from 'react';
import MaterialTable from 'material-table';
import Spinner from '../../components/UI/Spinner/Spinner';
import axios from 'axios';
import Icon from '@material-ui/core/Icon';
import { Menu, MenuItem } from '@material-ui/core';

const styles = {
  maxWidth: '90%',
  margin: '1em auto',
};

const InstructorCourses = (props) => {
  const [anchorEl, setAnchorEl] = useState(false);
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [group, setGroup] = useState([]);
  const [moreInfo, setMoreInfo] = useState(true);

  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, []);

  const request = () => {
    setLoading((loading) => !loading);
    //let url ='/instructor-courses'
    axios
      .get('/instructor-courses')
      .then((response) => {
        setData(response.data);
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        setLoading((loading) => !loading);
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const downloadAttendanceList = () => {
    //Se realizan dos consultas. La primera para obtener el nombre
    //del archivo y la segunda para obtener la cedula de inscripcin
    const firstUrl = '/auth/instructorcourses-filename/';
    const secondUrl = '/auth/instructorcourses-download/';

    const requestOne = axios.get(firstUrl + group);
    const requestTwo = axios({
      url: secondUrl + group,
      method: 'GET',
      responseType: 'blob',
    });

    axios
      .all([requestOne, requestTwo])
      .then(
        axios.spread((...responses) => {
          //Se guardan las respuestas
          const fileName = responses[0];
          const fileDownload = responses[1];
          //Se realiza la descarga de la lista de asistencia
          const downloadUrl = window.URL.createObjectURL(
            new Blob([fileDownload.data])
          );
          const link = document.createElement('a');
          link.href = downloadUrl;
          link.setAttribute('download', fileName.data.fileName); //any other extension
          document.body.appendChild(link);
          link.click();
          link.remove();
        })
      )
      .catch((errors) => {
        // react
      });
  };

  const openMenu = (event, row) => {
    setAnchorEl(event.currentTarget);
  };

  const closeMenu = (event, row) => {
    setAnchorEl(null);
  };

  const attendanceMenuRedirect = (group) => {
    props.history.push('/attendance-menu-instructor/' + group);
  };

  const partakersListRedirect = (group) => {
    props.history.push('/partakers-list/' + group);
  };

  const gradesListRedirect = (group) => {
    props.history.push('/grades-list/' + group);
  };

  let courseEnrollmentTable = (
    <Fragment>
      <MaterialTable
        title='Mis cursos (Instructor)'
        columns={[
          { title: 'Curso', field: 'course' },
          { title: 'Grupo', field: 'group' },
          { title: 'Aula', field: 'classroom' },
          { title: 'Periodo (dd/mm/aaaa)', field: 'period' },
          { title: 'Estatus', field: 'status' },
          { title: 'Departamento', field: 'department', hidden: moreInfo },
          {
            title: 'Tipo de capacitacion',
            field: 'trainingType',
            hidden: moreInfo,
          },
        ]}
        data={data}
        actions={[
          {
            icon: 'add_box',
            isFreeAction: true,
            tooltip: 'Mostrar mas informacin',
            onClick: () => {
              setMoreInfo((moreInfo) => !moreInfo);
            },
          },
          {
            icon: 'more_horiz',
            tooltip: 'Acciones',
            isFreeAction: false,
            onClick: (event, rowData) => {
              openMenu(event);
              setGroup(rowData.group);
            },
          },
        ]}
        options={{
          pageSize: 5,
          pageSizeOptions: [5, 10, 15, 20],
          headerStyle: {
            backgroundColor: '#7395ae',
            color: '#FFF',
            fontSize: '15px',
            fontWeight: 'bold',
          },
          filterCellStyle: {
            backgroundColor: '#01579b',
            color: '#FFF',
          },
          searchFieldStyle: {
            backgroundColor: '#E8E8E8',
            color: 'black',
          },
        }}
        localization={{
          header: {
            actions: 'MENU DE ACCIONES     ',
          },
          toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
          },
          pagination: {
            labelDisplayedRows: '{from}-{to} de {count}',
            labelRowsSelect: 'Filas',
            firstAriaLabel: 'Primera pagina',
            firstTooltip: 'Primera pagina',
            previousAriaLabel: 'Pagina anterior',
            previousTooltip: 'Pagina anterior',
            nextAriaLabel: 'Siguiente pagina',
            nextTooltip: 'Siguiente pagina',
            lastAriaLabel: 'Ultima pagina',
            lastTooltip: 'Ultima pagina',
          },
          body: {
            emptyDataSourceMessage: 'No cuenta con cursos asignados',
          },
        }}
      />
      <Menu
        id='simple-menu'
        keepMounted
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={closeMenu}
      >
        <MenuItem onClick={() => attendanceMenuRedirect(group)}>
          <Icon fontSize='small'>people</Icon> Tomar asistencia
        </MenuItem>

        <MenuItem
          onClick={() => {
            partakersListRedirect(group);
          }}
        >
          <Icon fontSize='small'>assignment</Icon> Ver lista de inscritos
        </MenuItem>

        <MenuItem
          onClick={() => {
            gradesListRedirect(group);
          }}
        >
          <Icon fontSize='small'>grade</Icon> Calificaciones
        </MenuItem>

        <MenuItem
          onClick={() => {
            downloadAttendanceList();
            closeMenu();
          }}
        >
          <Icon fontSize='small'>vertical_align_bottom</Icon> Descargar lista de
          asistencia
        </MenuItem>
      </Menu>
    </Fragment>
  );

  if (loading) {
    courseEnrollmentTable = <Spinner />;
  }

  return <div style={styles}>{courseEnrollmentTable}</div>;
};

export default InstructorCourses;
